.nav {
  &.main-menu {
    -webkit-box-shadow: 3px 0 7px 0px rgba(0, 117, 137, 0.6);
    box-shadow: 3px 0 7px 0px rgba(0, 117, 137, 0.6);

    > li {
      &.active {
        > a {
          background: #007589;
          outline: 0;

          .ico {
            .opacity(1);
          }
        }
      }
      > a {
        color: #fff;
        text-transform: uppercase;
        font-family: "Aller Display", "Aller";
        text-align: center;
        letter-spacing: 0.8px;
        padding: 14px 26px 0 26px;
        height: 70px;
        font-size: 12px;
        line-height: normal;
        span {
          margin-top: 3px;
        }

        -webkit-box-shadow: inset 5px 0 6px -2px rgba(0, 117, 137, 0.6);
        box-shadow: inset 5px 0 6px -2px rgba(0, 117, 137, 0.6);

        &:hover,
        &:active,
        &:focus {
          background: #007589;
          outline: 0;

          .ico {
            .opacity(1);
          }
        }
      }
      .ico {
        .opacity(.7);
        height: 19px;
      }
      .ico-tours {
        background-position: -86px -1px;
      }
      .ico-flight {
        background-position: -129px -3px;
      }
      // .ico-hotel {
      //   margin-top: 2px;
      // }
      .ico-document {
        background-position: -220px 0px;
      }
      .ico-blog {
        background-position: -264px -1px;
      }
    }
  }
  &.navbar-right {
    margin-top: 28px;
    > li {
      > a {
        color: #fff;
        line-height: 14px;
        padding: 0 10px;
        &:hover,
        &:focus {
          outline: 0;
          background: none;
        }
      }
    }
  }

  &.navbar-nav {
    > li {
      position: static;
    }
  }

  > li {

    // &.active {
    //   .form {
    //     display: block;
    //   }
    // }

    .form {
      display: none;
      .transition(all 0.5s ease-in-out);
    }

    &:hover, {
      .form {
        display: block;
      }
    }
  }
}

.navbar {
  z-index: 1001;
  margin-bottom: 0;
  background-color: @brand-primary;
  border-radius: 0;
  border-width: 0;
  -webkit-box-shadow: 0 -2px 0px #0d7f93, 0 1px 4px rgba(4, 7, 5, 0.2);
  box-shadow: inset 0 -2px 0px #0d7f93, 0 1px 4px rgba(4, 7, 5, 0.2);
  .navbar-brand {
    padding: 7px 20px 7px 10px;
    height: auto !important;
    display: block;

    &.md {
      padding-top: 20px;
    }
  }
}


.payment-nav {
  padding: 39px 0;
}

.payment-step {
  width: 589px;
  height: 42px;

  &-1 {
    background: url(../img/flight-payment-step-1.png) no-repeat 0 0;
  }

  &-2 {
    background: url(../img/flight-payment-step-2.png) no-repeat 0 0;
  }

  &-3 {
    background: url(../img/flight-payment-step-3.png) no-repeat 0 0;
  }

  &-4 {
    background: url(../img/flight-payment-step-4.png) no-repeat 0 0;
  }

  &-5 {
    background: url(../img/flight-payment-step-5.png) no-repeat 0 0;
  }



  a {
    display: block;
    float: left;
    width: 117px;
    line-height: 42px;
    text-align: center;
    font-family: "Gotham Black";
    color: #01414c;
    text-shadow: 1px 2px 0px rgba(61, 153, 169, 0.2);
    padding-left: 20px;

    &:last-child {
      padding-left: 0;
    }

    &:hover {
      color: #fff;
      text-shadow: 1px 2px 0px rgba(0, 0, 0, 0.2);
    }

    &.active {
      color: #000;
      text-shadow: none;

      &:hover {
        text-shadow: none;
      }
    }
  }
}
